import * as types from 'actionTypes';
import * as actions from 'actions';

import { of } from 'rxjs';
import { concatMap, map, catchError } from 'rxjs/operators';
import { ofType } from 'redux-observable';
import { ajax } from 'rxjs/ajax';

export const requestNetworkLayers = action$ => action$.pipe(
    ofType(types.REQUEST_NETWORK_LAYERS),
    concatMap(action =>
        ajax.getJSON(`/api/network/layers`).pipe(
            map(response => actions.receiveNetworkLayers(response)),
            catchError(error => of(actions.rejectNetworkLayers()))
        )
    )
);

export const requestNetworkClasses = action$ => action$.pipe(
    ofType(types.REQUEST_NETWORK_CLASSES),
    concatMap(action =>
        ajax.getJSON(`/api/network/classes`).pipe(
            map(response => actions.receiveNetworkClasses(response)),
            catchError(error => of(actions.rejectNetworkClasses()))
        ),
    )
);

export const requestImages = action$ => action$.pipe(
    ofType(types.REQUEST_IMAGES),
    concatMap(action =>
        ajax.getJSON(`/api/images`).pipe(
            map(response => actions.receiveImages(response)),
            catchError(error => of(actions.rejectImages()))
        )
    )
);

export const requestImageUpload  = action$ => action$.pipe(
    ofType(types.REQUEST_IMAGE_UPLOAD),
    concatMap(action =>
        ajax.post(`/api/images/`, action.payload ).pipe(
            map(response => actions.receiveImageUpload(response)),
            catchError(error => of(actions.rejectImageUpload()))
        )
    )
);

export default [
    requestNetworkLayers,
    requestNetworkClasses,
    requestImages,
    requestImageUpload,
];