import React, { Component } from 'react';
import { connect } from 'react-redux';

import { ImageGrid, Image } from 'mimir-ui';
import { requestImages, requestImageUpload } from 'actions';

class ImageSelectionGrid extends Component {
    
    constructor(props) {
        super(props);
        this.fileUpload = React.createRef();
    }

    componentDidMount() {
        this.props.requestImages();
    }
    
    getImageList = () => {
        
        if (!this.props.images) {
            return [];
        }

        return this.props.images.map(image => (
            <Image src={ image.imagefile } />
        ));
    }

    openUploadDialog = () => {
        this.fileUpload.current.click()
    }

    requestUpload = () => {
        let formData = new FormData();
        formData.append('imagefile', this.fileUpload.current.files[0]);
        this.props.requestImageUpload(formData);
    }    

    render() {
        return (
            <ImageGrid onClick={ this.openUploadDialog }>
                <input 
                    type='file'
                    ref={ this.fileUpload }
                    style={ { 'display': 'none' } }
                    onChange={ this.requestUpload }
                />
                { this.getImageList() }
            </ImageGrid>
        );
    }
}

export default connect(
    state => ({
        images: state.global.images,
    }),
    dispatch => ({
        requestImages: () => {
            dispatch(requestImages());
        },
        requestImageUpload: (formData) => {
            dispatch(requestImageUpload(formData));
        },
    })
)(ImageSelectionGrid);