import * as action from 'actionTypes';

export const actionCreator = type => {
    return (payload={}, meta={}, error={}) => ({
            type,
            payload,
            meta,
            error,
    });
}

export const selectLayer = () => { return actionCreator(action.SELECT_LAYER)(); }
export const selectClass = () => { return actionCreator(action.SELECT_CLASS)(); }

export const requestNetworkLayers = () => { return actionCreator(action.REQUEST_NETWORK_LAYERS)(); };
export const receiveNetworkLayers = () => { return actionCreator(action.RECEIVE_NETWORK_LAYERS)(); };
export const rejectNetworkLayers = () => { return actionCreator(action.REJECT_NETWORK_LAYERS)(); };

export const requestNetworkClasses = () => { return actionCreator(action.REQUEST_NETWORK_CLASSES)(); };
export const receiveNetworkClasses = () => { return actionCreator(action.RECEIVE_NETWORK_CLASSES)(); };
export const rejectNetworkClasses = () => { return actionCreator(action.REJECT_NETWORK_CLASSES)(); };

export const requestImages = () => { return actionCreator(action.REQUEST_IMAGES)(); };
export const receiveImages = response => { return actionCreator(action.RECEIVE_IMAGES)(response); };
export const rejectImages = response => { return actionCreator(action.REJECT_IMAGES)(response); };

export const requestImageUpload = formData => { return actionCreator(action.REQUEST_IMAGE_UPLOAD)(formData); };
export const receiveImageUpload = () => { return actionCreator(action.RECEIVE_IMAGE_UPLOAD)(); };
export const rejectImageUpload = () => { return actionCreator(action.REJECT_IMAGE_UPLOAD)(); };

export const receiveImagePrediction = () => { return actionCreator(action.RECEIVE_IMAGE_PREDICTION)(); };