import React, { Component } from 'react';
import { connect } from 'react-redux';

import { Grid, Image } from 'mimir-ui';

import NetworkClassSelection from 'components/NetworkClassSelection';
import NetworkLayerSelection from 'components/NetworkLayerSelection';
import ImageSelectionGrid from 'components/ImageSelectionGrid';

class AnalysisPage extends Component {
    render() {
        return (
            <Grid>
                <Grid.Column borderRight>
                    <Grid.Row borderBottom>
                        <Grid.Column borderRight>
                            <Image
                                src={ this.props.selected }
                            />
                        </Grid.Column>
                        <Grid.Column>
                            <Image
                                src={ this.props.heatmap }
                            />
                        </Grid.Column>
                    </Grid.Row>
                    <Grid.Row borderBottom>
                        <Grid.Column borderRight>
                            <Image
                                src={ this.props.saliency }
                            />
                        </Grid.Column>
                        <Grid.Column>
                            <Image
                                src={ this.props.guided }
                            />
                        </Grid.Column>
                    </Grid.Row>
                    <Grid.Row>
                        <Grid.Column borderRight>
                            <NetworkLayerSelection />
                        </Grid.Column>
                        <Grid.Column>
                            <NetworkClassSelection />
                        </Grid.Column>
                    </Grid.Row>
                </Grid.Column>
                <Grid.Column>
                    <ImageSelectionGrid />
                </Grid.Column>
            </Grid>
        );
    }
}

export default connect(
    state => ({
        selected: state.selected.image.base64,
        heatmap: state.selected.heatmap.base64,
        saliency: state.selected.saliency.base64,
        saliency: state.selected.guided.base64,
        prediction: state.selected.prediction,
        confidence: state.selected.confidence,
    })
)(AnalysisPage)
