import React, { Component } from 'react';
import { connect } from 'react-redux';

import { List } from 'mimir-ui';
import { requestNetworkLayers, selectLayer } from 'actions';

class NetworkLayerSelection extends Component {

    componentDidMount() {
        this.props.requestNetworkLayers();
    }

    getListRows = () => {

        if (!this.props.networkClasses) {
            return [];
        }
        
        return this.props.networkLayers.map(layer => (
            <List.Item
                selected={ layer.id === this.props.selectedLayer }
                onClick={ () => this.props.selectLayer(layer.id) }
            >
                { layer.name }
            </List.Item>
        ));
    }

    render() {
        return (
            <List>
                { this.getListRows() }
            </List>
        );
    }
}

export default connect(
    state => ({
        networkLayers: state.network.layers,
        selectedLayer: state.network.selectedLayer,
    }),
    dispatch => ({
        requestNetworkLayers: () => {
            dispatch(requestNetworkLayers());
        },
        selectLayer: layerId => {
            dispatch(selectLayer(layerId));
        },
    })
)(NetworkLayerSelection)