import * as actions from '../actionTypes';
import update from 'immutability-helper';

const initialState = {
    images: []
};

const global = (state = initialState, action) => {
    switch (action.type) {

        case actions.RECEIVE_IMAGES: {
            return update(state, {
                images: { $push: action.payload }
            })
        }
            
        default: {
            return state;
        }
    }
};

export default global;