import React, { Component } from 'react';
import { connect } from 'react-redux';

import { List } from 'mimir-ui';
import { requestNetworkClasses, selectClass } from 'actions';

class NetworkClassSelection extends Component {
    
    componentDidMount() {
        this.props.requestNetworkClasses();
    }

    getListRows = () => {

        if (!this.props.networkClasses) {
            return []
        }

        return this.props.networkClasses.map(category => (
            <List.Item
                selected={ category.id === this.props.selectedClass }
                onClick={ () => this.props.selectClass(category.id) }
            >
                { category.name }
            </List.Item>
        ));
    }

    render() {
        return (
            <List>
                { this.getListRows() }
            </List>
        );
    }
}

export default connect(
    state => ({
        networkClasses: state.network.classes,
        selectedClass: state.network.selectedClass,
    }),
    dispatch => ({
        requestNetworkClasses: () => {
            dispatch(requestNetworkClasses());
        },
        selectClass: classId => {
            dispatch(selectClass(classId));
        }
    })
)(NetworkClassSelection)

